* {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
  padding: 0;
}
body{
  background-color: whitesmoke;
}
.container{
  width: 90%;
  display: flex;
  gap: 15px;
  align-items: stretch;
  padding: 0;
  margin: 0 auto;
}
.month{
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  padding: 10;
  background-color: white;
  border-radius: 3px;
}
.results{
  flex: 1;
  border: none;
  padding: 10px;
  font-size: 14px;
  text-transform: uppercase;
}
.results::-webkit-scrollbar{
  display: none;
}
.drop{
  width: 200px;
  border: 1px dashed black;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 10px;
  cursor: pointer;
  position: relative;
}
.drop:hover{
  border: 1px dashed rgb(59, 255, 235);
}

.active{
  border: 1px dashed rgb(59, 255, 235);
}
.danger{
  border: 1px dashed tomato;
}
.plus{
  width: 100%;
}
.btn{
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: white;
  padding: 10px;
  width: 50px;
  cursor: pointer;
}
button{
  padding: 5px 10px;
  cursor: pointer;
}
.run{
  position: absolute;
  z-index: 99;
  right: 0;
  bottom: 0;
  border: none;
  outline: none;
}

.list_container{
  padding: 20px;
  display: flex;
  width: 100%;
  gap: 10px;
}
.item{
  padding: 5px 10px;
  background: white;
  cursor: pointer;
  transition: .3s all ease-in-out;
}
.item:hover{
  transform: scale(1.05);
  box-shadow: rgba(17, 17, 26, 0.05) 0px 4px 16px, rgba(17, 17, 26, 0.05) 0px 8px 32px;
}
.list{
  display: flex;
  justify-content: flex-start;
  align-items: stretch;
  gap: 10px;
  flex-direction: column;
  max-width: 30%;
}
.items{
  display: flex;
  justify-content: flex-start;
  align-items: stretch;
  gap: 2px;
  flex-direction: column;
}
.view{
  flex: 1;
  position: relative;
  height: 100vh;
}
.pdf{
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: white;
}
.pdfview{
  width: 100%;
  height: 100%;
  background-color: white;
}
.left{
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 10px;
  flex-direction: column;
  padding-top: 20px;
  padding-bottom: 20px;
}
.right{
  flex: 1;
  border: 1px solid white;
  min-height: 100vh;

}
.month{
  position: relative;
}
.rm{
  position: absolute;
  z-index: 999;
  padding: 5px;
  left: 169px;
  top: 1px;
  background-color: whitesmoke;
  width: 30px;
  cursor: pointer;
}
.rm:hover svg{
  color: tomato;
}